*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow-y: none;
}

.circle {
    position: fixed;
    display: none;
    bottom: 10px;
    right:3%;
    margin-bottom: 1rem;
    width: 50px;
    height: 50px;
    line-height: 10px;
    border-radius: 50%;
    font-size: 2rem;
    color: #fff;
    background: rgb(240, 50, 50);
    padding:10px;
    cursor: pointer;
    text-align:center;
    vertical-align: middle;
    box-shadow: 0px 0px 3px #000;
  }
  .circle i{
    color: #fff;
  }

.line{
    width: 50%;
    margin-left: 13rem;
    font-weight: 600;
    color: black;
}
.line2{
    width: 50%;        
    font-weight: 600;
    margin-right: 12rem;
    color: black;
    
}
.container1.mx-4{
    margin-top: 0.5rem;
    background-color: red;
    padding: 0.4rem;
    width: 0.4rem;
    height: 0.4rem;
}